.login{
  width: 120px;
    height: 50px;
    text-decoration: none;
    text-align: center;
    justify-content: center;

    border: none;
    outline: none;
    color: #fff;
    background: #111;
    cursor:pointer;
    display: flex;
    flex-direction:column;
    position: relative;
    z-index: 0;
    border-radius: 10px;
    margin: 10px;
    

}
.login::before{
  text-decoration: none;

  content: '';
    background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
    position: absolute;
    top: -2px;
    left:-2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;

}
.login:active{
  color: #000;
}
.login:active::after{
background: transparent;
text-decoration: none;

}
.login:hover::before{
  opacity: 1;
  text-decoration: none;

}
.login::after{
  z-index: -1;
  content: '';
  text-decoration: none;

  position: absolute;
  width: 100%;
  height: 100%;
  background: #111;
  left: 0;
  top: 0;
  border-radius: 10px;
}
.nav {
  display: flex;
  text-decoration: none;
  flex-wrap: wrap;
 background-color:black;
  /* padding: 10px 20px; */
  padding-bottom: -30px;
  opacity: 0.8;
  margin-bottom: 20px;
}
.nav{
  justify-content: center;
  padding:5px;
  
}






@keyframes glowing {
  0% { background-position: 0 0; }
  50% { background-position: 400% 0; }
  100% { background-position: 0 0; }
}